.c_select {
  box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.1), -6px -6px 10px -1px rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 30.5px;
  background-color: #f2f3f7;
  width: 250px;
  position: relative;
}

.c_select .c_select-placeholder {
  display: block;
  padding: 12px 20px 12px 45px;
  width: 100%;
  height: 100%;
  position: relative;
  cursor: pointer;
}

.c_select:after {
  display: none !important;
}

.c_select .c_select-placeholder:after {
  content: "";
  background: url("https://www.svgrepo.com/show/80156/down-arrow.svg") no-repeat;
  transition: all 0.5s ease;
  position: absolute;
  top: 52%;
  right: 28px;
  transform: translate(0, -52%);
  height: 11px;
  width: 9px;
}

.c_select.c_select-item-active .c_select-placeholder:after {
  transition: all 0.5s ease;
  transform: translate(0, -50%) rotate(180deg);
}

.c_select .c_select-block {
  bottom: auto;
  top: calc(100% + 3px);
  position: absolute;
  left: 0;
  z-index: 5;
}

.c_select.c_select-item-active .c_select-block {
  width: 100%;
  height: auto;
  max-height: 145px;
  background: #ffffff;
  overflow: auto;
}

.c_select .c_select-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  overflow-y: auto;
  height: 100%;
}

.c_select .c_select-block::-webkit-scrollbar {
  width: 27px;
}

.c_select .c_select-block::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 10px 10px #808080;
  box-shadow: inset 0 0 10px 10px #808080;
  border: solid 12px transparent;
}

.c_select .c_select-block::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 10px 10px #000;
  box-shadow: inset 0 0 10px 10px #000;
  border: solid 10px transparent;
}

.c_select .c_select-element {
  width: 100%;
  background: 0 0;
  color: #000;
  border: none;
  text-align: left;
  cursor: pointer;
  padding: 0px 12px;
  height: 35px;
}

.c_select .c_select-element-active {
  background: #f2f3f8;
  color: #5d61ae !important;
}

.thx_request {
  position: fixed;
  top: 0;
